import React, { useState } from 'react';
import TopBar from '../../components/TopBar';
import {
  RecruitContainer,
  ContentContainer,
  SubTitleText,
  Text
} from './style';
import Button from '../../components/Button';
import TitleText from '../../components/Text';
import RecruitList from './RecruitList';

const Recruit = () => {
  const [isScroll, setIsScroll] = useState(false);
  const handleScroll = () => {
    if (window.scrollY > 300) {
      setIsScroll(true);
    } else {
      setIsScroll(false);
    }
  }

  window.addEventListener('scroll', handleScroll);

  const openLink = () => {
    window.open('https://forms.gle/aF4mxuJZHMV84Ydj8', '_blank');
  }

  return (
    <RecruitContainer>
      <TopBar />
      <Button
        onClick={() => openLink()}
        text=""
        position={true}
        isScroll={isScroll}
      />
      <ContentContainer>
        <TitleText
          content="  "
          color="white"
          fontSize="64px"
          fontWeight="bold"
          animation={true}
        />
        <SubTitleText>2024 6 3 ~ 2024 7 1</SubTitleText>
        <RecruitList />
      </ContentContainer>
      <ContentContainer background={true}>
        <Text>   </Text>
        <Text color> !</Text>
        <Button
          onClick={() => openLink()}
          text=""
        />
      </ContentContainer>
    </RecruitContainer>
  );
}

export default Recruit;
